<template>
  <div>
    <mu-dialog :open="dialog" title="Alert Dialog">
      没有找到对应的示例,将为您跳转到博客列表页
      <mu-flat-button label="确定" slot="actions" primary @click="redirect"/>
    </mu-dialog>
    <example-page :text='text'></example-page>
  </div>
</template>
<script>
import example from '../example'
export default {
  data () {
    return {
      text: '',
      dialog: false
    }
  },
  mounted () {
    if (example[this.$route.params.name]) {
      this.text = example[this.$route.params.name]
    } else {
      this.open()
    }
  },
  methods: {
    open () {
      this.dialog = true
    },
    close () {
      this.dialog = false
    },
    redirect () {
      this.$router.push('/List/blog')
    }
  }
}
</script>



